import Vue from 'vue'
import Router from 'vue-router'
import HomeView from '@/components/HomeView'
import InsertReadingView from '@/components/InsertReadingView'
import DetailView from '@/components/DetailView'
import PostView from '@/components/PostView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeView,
      meta: {
        title: 'DMCare 糖訊通',
        burger: true
      }
    },
    {
      path: '/glucose/new',
      name: 'new',
      component: InsertReadingView,
      meta: {
        title: '新增度數',
        burger: false
      }
    },
    {
      path: '/detail/:id',
      name: 'detail',
      component: DetailView
    },
    {
      path: '/post',
      name: 'post',
      component: PostView
    }
  ]
})



