<template>
  <div class="mdc-layout-grid">
    <div class="spinner-bar" v-if="spinner_active">
    </div>
    <div class="mdc-layout-grid__inner">
    <div class="mdc-layout-grid__cell mdc-layout-grid__cell--span-12" >
      <button class="mdc-button mdc-button--unelevated"  v-for="tag in this.manga.labels"> {{ tag }}</button>
    </div>    
    <div class="mdc-layout-grid__cell mdc-layout-grid__cell--span-4-phone mdc-layout-grid__cell--span-8-desktop mdc-layout-grid__cell--span-6-tablet content" v-html="this.manga.content">
      {{ this.manga.content }}
    </div>
    </div>
  </div>
</template>
<script>
import Imager from 'imager.js'
import network from '../networkUtil'
export default {
  data () {
    return {
      'manga': [],
      'spinner_active': true
    }
  },
  mounted () {
    var contextHolder = this
    var getManga = function (key) {
      return new Promise((resolve, reject) => {
        var statement = key + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'
        resolve('https://www.googleapis.com/blogger/v3/blogs/' + process.env.BLOG_CODE + '/posts/' + statement)
      })
    }
    getManga(this.$route.params.id)
      .then((url) => network.promisedRequest('GET', url))
      .then((rawResponse) => {
        contextHolder.manga = JSON.parse(rawResponse)
        contextHolder.manga.content = contextHolder.manga.content.replace(new RegExp('/s1600/', 'g'), '/w{width}/')
        contextHolder.manga.content = contextHolder.manga.content.replace(new RegExp('src', 'g'), 'data-src')
        contextHolder.manga.content = contextHolder.manga.content.replace(new RegExp('<img', 'g'), '<img class="responsive-image"')
      })
      .then(() => {
        /* eslint-disable no-new */
        new Imager('.responsive-image', { availableWidths: [320, 400, 500, 600, 700, 800, 900, 1000, 1200, 1400, 1600] })
      })
      .then(() => { contextHolder.spinner_active = false })
  }
}
</script>
<style scoped>
@import "../../node_modules/@material/button/dist/mdc.button.css";
@import "../../node_modules/@material/layout-grid/dist/mdc.layout-grid.css";
  .info {
    text-align: right;
    padding: 5px;
    color: #555;
    font-size: 10px;
  }
  .spinner-bar {
    text-align: center;
  }
  .mdc-button {
    margin-right: 16px
  }
</style>


