<script>
  import {MDCTextField} from '@material/textfield'
  import {MDCSlider} from '@material/slider'
  import { nSQL } from 'nano-sql'
  
  export default {
    methods: {
      displayDetails (id, cached) {
        if (this.network.online || cached) {
          this.$router.push({name: 'detail', params: { id: id }})
        }
      },
      uploadChanges () {
        var contextHolder = this

        nSQL('glucose') //  "users" is our table name.
          .model([ // Declare data model
            { key: 'id', type: 'int', props: ['pk', 'ai'] }, // pk == primary key, ai == auto incriment
            { key: 'time', type: 'string' },
            { key: 'prandial', type: 'bool' },
            { key: 'glucose', type: 'float' },
            { key: 'remarks', type: 'string' }
          ])
          .config({
            mode: 'PERM' // pass in "PERM" to switch to persistent storage mode!
          })
          .connect() // Init the data store for usage. (only need to do this once)
          .then(function (result) {
            return nSQL().query('upsert', { // Add a record
              time: (new Date(contextHolder.year, contextHolder.month - 1, contextHolder.date, contextHolder.hour)).getTime(),
              glucose: contextHolder.glucose_level,
              prandial: contextHolder.prandial === 'true',
              remarks: contextHolder.remarks
            }).exec()
          })
          .then(function (result) {
            return nSQL().query('select').exec() // select all rows from the current active table
          })
          .then(function (result) {
            console.log(result) // <= [{id:1, name:"bill", age: 20}]
            var fab = document.querySelector('.mdc-fab')
            if (fab) fab.classList.add('mdc-fab--exited')
            contextHolder.$router.go(-1)
          })
      }
    },
    data () {
      return {
        'glucose_level': 0,
        'year': 0,
        'month': 0,
        'date': 0,
        'hour': 0,
        'prandial': 'true',
        'remarks': ''
      }
    },
    mounted () {
      var contextHolder = this
      var now = new Date()

      contextHolder.year = now.getFullYear()
      contextHolder.month = now.getMonth() + 1
      contextHolder.date = now.getDate()
      contextHolder.hour = now.getHours()

      var remarks = new MDCTextField(document.querySelector('.remarks'))
      console.log(remarks)

      const slider = new MDCSlider(document.querySelector('.mdc-slider'))
      slider.listen('MDCSlider:input', function (detail) {
        contextHolder.glucose_level = Math.round(slider.value * 10) / 10
      })
    }
  }
</script>
