<template>
    <div class="mdc-layout-grid">
    <!-- alert cards-->
<section class="mdc-card mdc-card--outlined">
  <div class="mdc-card__media mdc-card__media--square">
    <div class="mdc-card__media-content">
  做得好！餐後度數比上星期進步</div>
  </div>
  <div class="mdc-card__actions">
    <div class="mdc-card__action-buttons">
      <button class="mdc-button mdc-card__action mdc-card__action--button">知道了</button>
    </div>
  </div>
</section>
    <!-- recent readings-->
<section class="hero centre">
      <div class="mdc-layout-grid__inner text_centre">

    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-layout-grid__cell--span-6-desktop mdc-typography--headline6">飯前</h5>
    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-layout-grid__cell--span-6-desktop mdc-typography--headline6">飯後</h5>
    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-layout-grid__cell--span-6-desktop mdc-typography--headline2">{{fasting}}</h5>
    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-layout-grid__cell--span-6-desktop mdc-typography--headline2">{{postprandial}}</h5>
	   </div>

  </section>
      <section class="centre mdc-layout-grid__inner">
        <button class="mdc-button mdc-button--outlined">
          <i class="material-icons mdc-button__icon" aria-hidden="true">add_alert</i>
          度數提示
        </button>  
        <button class="mdc-button mdc-button--outlined">
          <i class="material-icons mdc-button__icon" aria-hidden="true">bar_chart</i>
          趨勢圖
        </button>
      </section>
    <router-link class="mdc-fab mdc-fab--extended app-fab--absolute" to="/glucose/new">
      <span class="material-icons mdc-fab__icon">add</span>
      <span class="mdc-fab__label">新增度數</span>
    </router-link>
  </div>
</template>
<script>
  import network from '../networkUtil'
  import {MDCRipple} from '@material/ripple'
  import { nSQL } from 'nano-sql'
  
  export default {
    methods: {
      displayDetails (id, cached) {
        if (this.network.online || cached) {
          this.$router.push({name: 'detail', params: { id: id }})
        }
      }
    },
    data () {
      return {
        'pictures': [],
        'spinner_active': true,
        'network': network,
        'fasting': '--',
        'postprandial': '--'
      }
    },
    mounted () {
      var contextHolder = this
      const fabRipple = new MDCRipple(document.querySelector('.mdc-fab'))
      console.log(fabRipple)

      nSQL('glucose') //  "users" is our table name.
        .model([ // Declare data model
          { key: 'id', type: 'int', props: ['pk', 'ai'] }, // pk == primary key, ai == auto incriment
          { key: 'time', type: 'string' },
          { key: 'prandial', type: 'bool' },
          { key: 'glucose', type: 'float' },
          { key: 'remarks', type: 'string' }
        ])
        .config({
          mode: 'PERM' // pass in "PERM" to switch to persistent storage mode!
        })
        .connect()
        .then(function (result) {
          return nSQL().query('select')
            .where(entry => entry.prandial) // Having uses the same syntax as WHERE, but runs after the GROUP BY command.
            .orderBy({time: 'desc'})
            .limit(1)
            .exec() // select all rows from the current active table
        })
        .then(function (result) {
          if (result.length > 0) contextHolder.postprandial = result[0].glucose
        })
        .then(function (result) {
          return nSQL().query('select')
            .where(entry => !entry.prandial) // Having uses the same syntax as WHERE, but runs after the GROUP BY command.
            .orderBy({time: 'desc'})
            .limit(1)
            .exec() // select all rows from the current active table
        })
        .then(function (result) {
          if (result.length > 0) contextHolder.fasting = result[0].glucose
        })

      // Fetch the latest data.
      var resolveURL = function (paginate) {
        return new Promise((resolve, reject) => {
          var statement = ''
          if (paginate == null) {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true'
          } else {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true&pageToken=' + paginate
          }
          resolve('https://www.googleapis.com/blogger/v3/blogs/' + process.env.BLOG_CODE + '/posts' + statement)
        })
      }
      function fetchList (paginate) {
        return resolveURL(paginate)
          .then((url) => network.promisedRequest('GET', url))
          .then((rawResponse) => {
            var response = JSON.parse(rawResponse)
            console.log(response)
            response.items.forEach((postAbstract) => {
              if (postAbstract.images[0] != null) {
                postAbstract.image = postAbstract.images[0].url.replace('/s1600/', '/w320/') // downsample image size
              }
              postAbstract.cached = false
              if ('caches' in window) {
                caches.open('blog-content').then((cache) => cache.match(postAbstract.selfLink + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'))
                  .then((response) => {
                    if (typeof response !== 'undefined') {
                      postAbstract.cached = true
                      return response.json().then((json) => {
                        var exp = /src="(.*?)"/g
                        var list = []
                        var match
                        do {
                          match = exp.exec(json.content)
                          if (match) {
                            list.push(match[1])
                          }
                        } while (match)
                        console.log(list)
                        caches.open('manga-images').then((cache) => cache.keys())
                          .then((keys) => {
                            console.log(keys)
                            list.forEach((key) => {
                              // function to find imageurl in cache
                              if (keys.findIndex((request) => {
                                var url = request.url.replace(/(s1600|w320|w400|w500|w600|w700|w800|w900|w1000|w1200|w1400|w1600)+/g, 's1600')
                                return (url === key)
                              }) === -1) {
                                postAbstract.cached = false
                                console.log(key, ' is not found!')
                              }
                            })
                          })
                      })
                    }
                  })
              }
              contextHolder.pictures.push(postAbstract)
            })
            if (response.nextPageToken != null) {
              return fetchList(response.nextPageToken)
            }
          })
      }
      fetchList().then(() => { contextHolder.spinner_active = false })
    }
  }
</script>
<style scoped>
@import "../../node_modules/@material/typography/dist/mdc.typography.css";
@import "../../node_modules/@material/layout-grid/dist/mdc.layout-grid.css";
@import "../../node_modules/@material/grid-list/dist/mdc.grid-list.css";
@import "../../node_modules/@material/linear-progress/dist/mdc.linear-progress.css";
@import "../../node_modules/@material/button/dist/mdc.button.css";
@import "../../node_modules/@material/card/dist/mdc.card.css";
@import "../../node_modules/@material/fab/dist/mdc.fab.css";
@import "../../node_modules/@material/chips/dist/mdc.chips.css";

  .mdc-grid-tile__primary-content {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  .mdc-grid-tile {
    --mdc-grid-list-tile-width: 300px;
  }
  .mdc-grid-tile__secondary, .overlay {
    background: rgba(0, 0, 0, 0.5);
  }
  .overlay {
    height: -webkit-fill-available;
  }
  .spinner-bar {
    text-align: center;
  }
  .hero {
      height: 240px;
      min-height: 240px;
  }
  .centre {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
  }
  .text_centre {
      text-align: center;
  }
.app-fab--absolute {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
}

@media(min-width: 1024px) {
   .app-fab--absolute {
    bottom: 1.5rem;
    right: 1.5rem;
  }
}
</style>


