export default class Notif {
  constructor (registration) {
    this.swRegistration = registration
    this.applicationServerPublicKey = 'BN-EF4YiddWuj_44RlexFoJZ-wEDpFeBWkDh_R8YukIui2uHGNCppcJo88Z-mVepwW1sjW1dfYcmGiCJeJY8ib4'
    this.isSubscribed = false
  }
  updateSubscriptionOnServer (subscription) {
    // TODO: Send subscription to application server
    if (subscription) {
      console.log(JSON.stringify(subscription))
    }
  }
  subscribeUser () {
    var urlB64ToUint8Array = (base64String) => {
      const padding = '='.repeat((4 - base64String.length % 4) % 4)
      const base64 = (base64String + padding)
        .replace(/-/g, '+')
        .replace(/_/g, '/')
      const rawData = window.atob(base64)
      const outputArray = new Uint8Array(rawData.length)
      for (let i = 0; i < rawData.length; ++i) {
        outputArray[i] = rawData.charCodeAt(i)
      }
      return outputArray
    }
    const applicationServerKey = urlB64ToUint8Array(this.applicationServerPublicKey)
    var contextHolder = this
    this.swRegistration.pushManager.subscribe({
      userVisibleOnly: true,
      applicationServerKey: applicationServerKey
    })
      .then(function (subscription) {
        console.log('User is subscribed.')
        contextHolder.updateSubscriptionOnServer(subscription)
        contextHolder.isSubscribed = true
      })
      .catch(function (err) {
        console.log('Failed to subscribe the user: ', err)
      })
  }
  unsubscribeUser () {
    var contextHolder = this
    this.swRegistration.pushManager.getSubscription()
      .then(function (subscription) {
        if (subscription) {
          return subscription.unsubscribe()
        }
      })
      .catch(function (error) {
        console.log('Error unsubscribing', error)
      })
      .then(function () {
        contextHolder.updateSubscriptionOnServer(null)
        console.log('User is unsubscribed.')
        contextHolder.isSubscribed = false
      })
  }
}



